@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);

body {
  font-family: "Heebo", sans-serif;
}

.App {
  width: 500px;
  text-align: center;
  margin: 0 auto;
}

.goods__main {
  width: 80px;
  height: 40px;
  font-family: inherit;
  text-transform: uppercase;
  outline: none;
  background-color: #f92e27;
  border: none;
  color: #fff;
  border-radius: 6%;
  cursor: pointer;
}

.goods__item {
  height: 35px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #bfbebe;
}

.goods__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.goods__buttons {
  margin: 20px 0;
}

.goods__button {
  text-transform: uppercase;
  font-family: inherit;
  outline: none;
  font-size: 14px;
  cursor: pointer;
  border: none;
  transition: 0.3s;
}

.goods__button-reverse,
.goods__button-alphabet,
.goods__button-length {
  display: inline-block;
  margin-right: 10px;
  color: white;
  height: 40px;
  background-color: #3ab3eb;
}

.goods__button-reset {
  width: 80px;
  height: 40px;
  background-color: #f92e27;
  color: white;
}

.goods__button:hover {
  -webkit-filter: brightness(95%);
          filter: brightness(95%);
}

.goods__selection {
  width: 100%;
  height: 30px;
  outline: none;
}

.goods__selection:focus {
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  border: 1px solid rgba(81, 203, 238, 1);
}

